import React from 'react'
import styled from 'styled-components'
import Project from './Project'
import {v4 as uuid} from "uuid"

export default function Projects() {
    
    const projectData = [
        { id: "1", name: "Rentomojo clone", img: "Rentomojo.jpg", gh:"https://github.com/SayedMajid/rento-mojo-clone", url:"https://csb-v02nso.netlify.app/", desc: "Rentomojo is a online rental company who provides rental services for everything . A collaborative project, developed along with 5 team members in 4 days.", stack:"React, Redux,Redux-Thunk, ChakraUI, Styled-Components"},
        { id: "2", name: "Anthropologie  Clone", img: "Anthoropology.png", gh:"https://github.com/HITTPATEL/Anthropologie-Clone", url:"https://dancing-custard-b04645.netlify.app/index.html", desc: "Anthropologie is a life style branding that impacts a sence of beauty, Optimism and discovery to its costomers. A collaborative project, developed along with 5 team members in 4 days.", stack:"HTML, CSS, JavaScript"},
        { id: "3", name: "Shop Disney Clone", img: "ShopDisney.png", gh:"https://github.com/shreymishra27/Unit-2-Project", url:"https://capable-pudding-1f0353.netlify.app/", desc: "Disney Store was an international chain of speciality stores selling only Disney related items.A collaborative project, developed along with 6 team members in 4 days. ", stack:"HTML, CSS, JavaScript"},
        
        // { id: "4", name: "Angel.co - clone", img: "angel1.png", gh:"https://github.com/Deependra-Negi/project-angel", url:"https://angellist.netlify.app/", desc: "Website for startups to hire, and job-seekers looking to work at startups. A collaborative project, developed along with 3 team members in 6 days.", stack:"React, Redux, MaterialUI, Styled-Components"},
        // { id: "5", name: "Internshala - clone", img: "internshala2.png", gh:"https://github.com/Deependra-Negi/internshala_project", url:"https://pulkit0111.github.io/internshala_project/", desc: "An online internship and training platform. A collaborative project, developed along with 3 team members in 10 days.", stack:"HTML, CSS, JavaScript"}
    ];

    return (
        <Cont id='projects' key={uuid()}>
            <Heading>Projects</Heading>
            <ProjectsCont>
                <Project data={projectData}/>
            </ProjectsCont>
        </Cont>
    )
}

const Cont = styled.div`
    background-color: #cce8ffb8;
    position: relative;
`
const Heading = styled.div`
    padding-top: 5rem;
    text-align:center ;
    font-size: 2rem;
    font-weight: 300;
`
const ProjectsCont = styled.div`
    margin: 5rem auto 0;
    width: 70vw;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    @media (max-width: 1264px) {
        margin: 2rem auto 0;
        width: 80vw;
    }
    @media (max-width: 768px) {
        margin: 2rem auto 0;
        width: 90vw;
        padding-bottom: 3rem;
    }
    @media (max-width: 500px) {
        margin: auto;
        width: 100vw;
        padding-bottom: 3rem;
    }
`